.featured {
  width: 100%;
  display: flex;
  justify-content: space-between;
}
.featuredItem {
  flex: 1;
  margin: 0.85rem 1.25rem;
  padding: 10px 30px;
  border-radius: 5px;
  cursor: pointer;
  box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -webkit-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -moz-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
}
.featuredTitle {
  font-size: 1.25rem;
}
.featuredMoneyContainer {
  margin: 10px 0px;
  display: flex;
  align-items: center;
}
.featureddMoney {
  font-size: 30px;
  font-weight: 600;
}
.featuredMoneyRate {
  display: flex;
  align-items: center;
  margin-left: 10px;
}
.featuredIcon {
  font-size: 1.2rem;
  margin-left: 5px;
}
.negative {
  color: #f44336;
}
.positive {
  color: #4caf50;
}
.featuredSub {
  color: grey;
  font-size: 15px;
}
@media screen and (max-width: 600px) {
  .featured {
    display: flex;
    flex-direction: column;
  }
}
