.userListActions {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.userListEdit {
  border: none;
  padding: 5px 10px;
  border-radius: 5px;
  background-color: #fbc02d;
  margin-right: 10px;
  cursor: pointer;
}
.userListDelete {
  color: #dd2c00;
  cursor: pointer;
}
