.widgetLg {
  flex: 2;
  border-radius: 10px;
  padding: 20px;
  box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -webkit-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -moz-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
}
.widgetLgTitle {
  font-size: 1.3rem;
  font-weight: 600;
}
.widgetTable {
  width: 100%;
  border-spacing: 20px;
}
.widgetLgTh {
  text-align: left;
}
.widgetLgTrUser {
  display: flex;
  align-items: center;
  font-weight: 600;
}
.widgetLgImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.widgetLgDate {
  font-weight: 200;
}
.widgetLgAmount {
  font-weight: 200;
}
.widgetLgBtn {
  border: none;
  padding: 5px 7px;
  border-radius: 10px;
  transition: transform 0.1s;
}
.widgetLgBtn:hover {
  transform: scale(1.08);
}
.Approved {
  background-color: #4db6ac;
  color: #004d40;
}
.Declined {
  background-color: #ef5350;
  color: #b71c1c;
}
.Pending {
  background-color: #64b5f6;
  color: #0d47a1;
}
