.widgetSm {
  flex: 1;
  border-radius: 10px;
  margin-right: 20px;
  box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -webkit-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -moz-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  padding: 20px;
}
.widgetSmTitle {
  font-size: 1.3rem;
  font-weight: 600;
}
.widgetSmList {
  margin: 0 !important;
  padding: 0 !important;
  list-style: none;
}
.widgetSmListItem {
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  margin: 20px 0px;
}
.widgetSmImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}
.widgetSmUser {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
}
.widgetSmUserName {
  font-weight: 500;
  font-size: 1rem;
}
.widgetSmUserTitle {
  font-weight: 300;
  font-size: 0.825rem;
}
.widgetSmButton {
  border: none;
  display: flex;
  align-items: center;
  padding: 2px 5px;
  justify-content: space-between;
  background-color: #eeeef7;
  color: #555;
  cursor: pointer;
}
.widgetSmIcon {
  font-size: 12px;
  margin-right: 4px;
}
