.user {
  padding: 20px;
}
.userTitleContainer {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.userAddButton {
  border: none;
  padding: 8px 10px;
  border-radius: 10px;
  background-color: #0097a7;
  color: aliceblue;
  cursor: pointer;
  font-size: 1rem;
  transition: transform 0.2s;
}
.userAddButton:hover {
  transform: scale(1.08);
}
.userContainer {
  display: flex;
}
.userShow {
  flex: 1;
  padding: 15px;
  margin-top: 20px;
  box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -webkit-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -moz-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
}
.userDetails {
  display: flex;
  flex-direction: column;
  padding-top: 5px;
  padding-left: 5px;
  padding-right: 5px;
}
.userDetails h4 {
  margin-bottom: 15px;
}
.userDetails h4 {
  margin-bottom: 15px;
}
.userStatusContainer {
  width: 100%;
  display: flex;
  justify-content: center;
}
.userStatus {
  padding: 8px 12px;
  background-color: #388e3c;
  color: #fff;
  border-radius: 10px;
  cursor: pointer;
  transition: transform 0.2s;
}
.userStatus:hover {
  transform: scale(1.08);
}
.userUpdate {
  flex: 2;
  padding: 15px;
  margin-left: 10px;
  box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -webkit-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
  -moz-box-shadow: 8px 8px 7px -1px rgba(0, 0, 0, 0.18);
}
.userUpdateTitle {
  width: 50px;
  margin: 0 auto;
  font-weight: 600;
  font-size: 1.125rem;
}
.userUpdateForm {
  padding: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.userUpdateItem {
  margin-bottom: 10px;
}
