.topbar {
  width: 100%;
  height: 50px;
  background-color: #e0f2f1;
  position: sticky;
  top: 0;
}
.topbarWrapper {
  height: 100%;
  padding: 0rem 1.25rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.logo {
  font-weight: 700;
  font-size: 1.8rem;
  color: #0d47a1;
  cursor: pointer;
}
.logoMobile {
  display: none;
}
.topRight {
  display: flex;
  align-items: center;
}
.topbarIconsContainer {
  margin-right: 14px;
  cursor: pointer;
  transition: transform 0.2s;
}
.topbarIconsContainer:hover {
  transform: scale(1.08);
}
.TopbarProfileImage {
  width: 2rem;
  height: 2rem;
  border-radius: 50%;
  cursor: pointer;
}
@media screen and (max-width: 600px) {
  .logo {
    font-size: 1.25rem;
    display: none;
  }
  .logoMobile {
    display: block;
  }
  .topRight {
    font-size: 1.25rem;
  }
  .topbarIconsContainer {
    margin-right: 14px;
    font-size: 1.5rem;
  }
  .TopbarProfileImage {
    width: 2rem;
    height: 2rem;
    border-radius: 50%;
  }
}
