.sidebar {
  height: calc(100vh - 50px);
  position: sticky;
  top: 50px;
  background-color: #1e88e5;
}
.sidebarWrapper {
  padding: 20px;
  color: black;
}
.sidebarMenu {
  margin-bottom: 1.25rem;
}
.sidebarTitle {
  font-size: 1.25rem;
  color: #424242;
  margin-bottom: 10px;
}
.sidebarList {
  padding: 5px;
}
.sidebarListItem {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: 5px;
  cursor: pointer;
  border-radius: 10px;
  margin-bottom: 5px;
}
.sidebarList .active {
  background-color: #c5e1a5;
}
.sidebarListItem:hover {
  background-color: #e6ee9c;
}
.sidebarIcon {
  margin-right: 5px;
  font-size: 16px;
}
.sidebarIconText {
  font-size: 16px;
}
@media screen and (max-width: 600px) {
  .sidebarWrapper {
    padding: 3px;
  }
  .sidebarMenu {
    margin-bottom: 1rem;
  }
  .sidebarTitle {
    font-size: 1rem;
    margin-bottom: 2px;
    letter-spacing: -1px;
  }
  .sidebarListItem {
    justify-content: center;
  }
  .sidebarIconText {
    display: none;
  }
}
